.fol-graphexplorer-cYTZ7LnVXZ body {
  user-select: none;
  /*fix the bug, when text is dragged over canvas and cause loading of different element (text - element)*/
}
.fol-graphexplorer-cYTZ7LnVXZ .btn-graph {
  outline: none;
  color: black;
  text-align: center;
  font-size: 13px;
  padding: 0 0.3em;
  font-weight: bold;
  border: 0;
}
.fol-graphexplorer-cYTZ7LnVXZ .no-padding-right {
  padding-right: 0 !important;
}
.fol-graphexplorer-cYTZ7LnVXZ .teacher-mode:hover {
  background-color: #28a745;
}
.fol-graphexplorer-cYTZ7LnVXZ .form-control.is-invalid {
  background-image: none;
}
.fol-graphexplorer-cYTZ7LnVXZ .hidden-on-medium-and-lower {
  display: inline;
}
.fol-graphexplorer-cYTZ7LnVXZ .vh-pane-left {
  height: calc(100vh - 64px);
  padding-right: 15px;
}
.fol-graphexplorer-cYTZ7LnVXZ .vh-pane-right {
  height: calc(100vh - 64px);
  padding-right: 15px;
  padding-left: 15px;
}
.fol-graphexplorer-cYTZ7LnVXZ .viewMode .vh-pane-left {
  height: unset !important;
}
.fol-graphexplorer-cYTZ7LnVXZ .viewMode .vh-pane-right {
  height: unset !important;
}
@media all and (max-width: 991px) {
  .fol-graphexplorer-cYTZ7LnVXZ .vh-pane-left {
    height: calc((100vh - 75px) / 2);
    margin-bottom: 5px;
  }
  .fol-graphexplorer-cYTZ7LnVXZ .vh-pane-right {
    height: calc((100vh - 75px) / 2);
    margin-top: 5px;
    padding-left: 0;
  }
  .fol-graphexplorer-cYTZ7LnVXZ .hidden-on-medium-and-lower {
    display: none;
    width: max-content;
  }
  .fol-graphexplorer-cYTZ7LnVXZ .container-fluid {
    padding-right: 0;
    overflow: hidden;
  }
}
