.tableaueditor-Obry4K9MqH body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
.tableaueditor-Obry4K9MqH .tableau {
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
}
.tableaueditor-Obry4K9MqH .formula {
  display: block;
  border-top: 0.5px solid transparent;
  min-width: 100%;
  width: max-content;
}
.tableaueditor-Obry4K9MqH .beta {
  min-width: 100%;
}
.tableaueditor-Obry4K9MqH .formula > .beta {
  display: inline-flex;
}
.tableaueditor-Obry4K9MqH .beta > .formula {
  display: inline-block;
  min-width: 20px;
  flex-grow: 1;
  border-top: 0.5px solid black;
  border-left: 0.5px solid black;
  padding-left: 2.25px;
}
.tableaueditor-Obry4K9MqH .beta > .formula:first-child {
  border-left: none;
  padding-left: 0;
  padding-right: 2.25px;
}
.tableaueditor-Obry4K9MqH .inputGroup {
  display: flex;
  font-size: 0.75rem;
  align-items: baseline;
  margin-bottom: 0.125em;
  margin-bottom: 0.125rem;
}
.tableaueditor-Obry4K9MqH .formulaId {
  width: 0.625em;
}
.tableaueditor-Obry4K9MqH .textInput {
  text-align: center;
  background-color: inherit;
  font-size: 0.75rem;
  border: 0;
  outline: 0;
  padding-bottom: 1px;
  border-bottom: 1px dashed lightgray;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}
.tableaueditor-Obry4K9MqH .textInput:focus {
  background-color: rgba(0, 0, 0, 0.05);
  padding-bottom: 0;
  border-bottom: 2px solid #16a;
}
.tableaueditor-Obry4K9MqH .textInput:hover {
  background-color: rgba(0, 0, 0, 0.075);
  border-bottom-style: solid;
}
.tableaueditor-Obry4K9MqH .textInput:active {
  background-color: rgba(0, 0, 0, 0.1);
  border-bottom-style: solid;
}
.tableaueditor-Obry4K9MqH .textInputFormula,
.tableaueditor-Obry4K9MqH .textInputFormulaWithSubst {
  flex-grow: 1;
  /* width: calc(100% - 5rem); */
}
.tableaueditor-Obry4K9MqH .textInputFormulaWithSubst {
  /* width: calc(100% - 11.25rem); */
}
.tableaueditor-Obry4K9MqH .textInputSubst,
.tableaueditor-Obry4K9MqH .textInputReference {
  flex: none;
  min-width: 1rem;
}
.tableaueditor-Obry4K9MqH .expandControls {
  text-align: center;
  margin-bottom: 0.125em;
  margin-bottom: 0.125rem;
}
.tableaueditor-Obry4K9MqH .actions button {
  margin: 0 0.5em;
}
.tableaueditor-Obry4K9MqH .actions {
  display: flex;
  justify-content: flex-end;
}
.tableaueditor-Obry4K9MqH .problems {
  max-width: 97%;
}
.tableaueditor-Obry4K9MqH .button,
.tableaueditor-Obry4K9MqH .onclick-menu:before,
.tableaueditor-Obry4K9MqH .onclick-menu.change,
.tableaueditor-Obry4K9MqH .onclick-menu.add,
.tableaueditor-Obry4K9MqH .onclick-menu.del {
  padding: 2px 10px;
  background-color: #fff;
  border-radius: 20px;
  border: thin solid lightgray;
  font-size: 0.75rem;
}
.tableaueditor-Obry4K9MqH .button:focus,
.tableaueditor-Obry4K9MqH .onclick-menu:focus:before,
.tableaueditor-Obry4K9MqH .onclick-menu.change:focus,
.tableaueditor-Obry4K9MqH .onclick-menu.add:focus,
.tableaueditor-Obry4K9MqH .onclick-menu.del:focus {
  background-color: rgba(0, 0, 0, 0.05);
  border-color: #16a;
}
.tableaueditor-Obry4K9MqH .button:hover,
.tableaueditor-Obry4K9MqH .onclick-menu:hover:before {
  background-color: rgba(0, 0, 0, 0.075);
  border-color: #ccc;
}
.tableaueditor-Obry4K9MqH .onclick-menu-content button:hover,
.tableaueditor-Obry4K9MqH .onclick-menu-content button:focus {
  background-color: rgba(0, 0, 0, 0.075);
}
.tableaueditor-Obry4K9MqH .button.active {
  background-color: rgba(0, 0, 0, 0.1);
  border-color: #bbb;
}
.tableaueditor-Obry4K9MqH .button:active,
.tableaueditor-Obry4K9MqH .onclick-menu:active:before {
  background-color: rgba(0, 0, 0, 0.1);
  border-color: #16a;
}
.tableaueditor-Obry4K9MqH .onclick-menu-content button:active {
  color: white;
  background-color: #16a;
}
.tableaueditor-Obry4K9MqH .closed {
  min-width: 17px;
}
.tableaueditor-Obry4K9MqH .nodeProblems {
  text-align: center;
  color: hsl(36, 100%, 40%);
  font: small-caption;
  font-weight: bold;
  font-size: 0.75rem;
}
.tableaueditor-Obry4K9MqH .nodeProblemsText {
  display: block;
}
.tableaueditor-Obry4K9MqH .onclick-menu {
  position: relative;
  display: inline-block;
}
.tableaueditor-Obry4K9MqH .onclick-menu.change .onclick-menu-content {
  left: auto;
  right: -2em;
}
.tableaueditor-Obry4K9MqH .onclick-menu.add {
  margin-left: 5px;
}
.tableaueditor-Obry4K9MqH .onclick-menu:focus {
  pointer-events: none;
}
.tableaueditor-Obry4K9MqH .onclick-menu:focus .onclick-menu-content {
  opacity: 1;
  visibility: visible;
}
.tableaueditor-Obry4K9MqH .onclick-menu-content {
  pointer-events: auto;
  position: absolute;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  transition: visibility 0.25s, opacity 0.1s;
  left: -1em;
}
.tableaueditor-Obry4K9MqH .onclick-menu.no-pointer-events {
  pointer-events: auto !important;
}
.tableaueditor-Obry4K9MqH .onclick-menu.no-visibility .onclick-menu-content {
  visibility: visible !important;
  display: none;
}
.tableaueditor-Obry4K9MqH .onclick-menu.no-visibility:focus .onclick-menu-content {
  display: block;
}
.tableaueditor-Obry4K9MqH .onclick-menu.no-opacity .onclick-menu-content {
  opacity: 1 !important;
}
.tableaueditor-Obry4K9MqH .onclick-menu {
  font: caption-small;
  font: 400 11px system-ui;
  font: -moz-button;
  padding: 0;
  margin: 0;
  outline: 0;
  margin-right: 0.3em;
  text-align: left;
}
.tableaueditor-Obry4K9MqH .onclick-menu-content {
  background-color: white /* #364656 */;
  width: auto;
  margin-top: 5px;
  margin-left: 0;
  padding: 0.25em 0;
  border-radius: 0.25em;
  border: thin solid lightgray;
}
.tableaueditor-Obry4K9MqH .onclick-menu-content li {
  color: black /* #f2f5e9 */;
  list-style-type: none;
  white-space: nowrap;
}
.tableaueditor-Obry4K9MqH .onclick-menu-content button {
  background: transparent;
  border: none;
  color: inherit;
  cursor: inherit;
  outline: 0;
  cursor: pointer;
  line-height: 2em;
  padding: 0 2em;
  font-size: 0.75rem;
  width: 100%;
  text-align: left;
  margin: 0;
}
.tableaueditor-Obry4K9MqH .actions #importJson {
  display: none;
}
.tableaueditor-Obry4K9MqH button.delete {
  padding: 0 0.3em;
  border: thin solid lightgray;
  background-color: inherit;
  color: gray;
}
.tableaueditor-Obry4K9MqH .assumption {
