}
.tableaueditor-Obry4K9MqH .half {
  width: 50%;
  float: left;
  padding: 0 0.5rem 0 0;
  box-sizing: border-box;
}
.tableaueditor-Obry4K9MqH .full {
  clear: left;
  width: 100%;
  float: left;
  box-sizing: border-box;
}
.tableaueditor-Obry4K9MqH .withWhiteSpace {
  white-space: pre;
}
.tableaueditor-Obry4K9MqH .rules-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.tableaueditor-Obry4K9MqH .ruleBox {
