  }
}
@media print {
  .tableaueditor-Obry4K9MqH {
    /* Print color backgrounds for webkit browsers */
  }
  .tableaueditor-Obry4K9MqH body > div > *:not(.formula):not(.verdict):not(.problems),
  .tableaueditor-Obry4K9MqH .onclick-menu,
  .tableaueditor-Obry4K9MqH .nodeProblems,
  .tableaueditor-Obry4K9MqH .button {
    display: none;
  }
  .tableaueditor-Obry4K9MqH .closed {
