  }
  .tableaueditor-Obry4K9MqH .textInput {
    border-bottom: none;
  }
  .tableaueditor-Obry4K9MqH .textInput.syntaxProblem,
  .tableaueditor-Obry4K9MqH .textInput.semanticsProblem {
    border-bottom-style: dashed;
    border-bottom-width: 1px;
  }
  .tableaueditor-Obry4K9MqH body {
    -webkit-print-color-adjust: exact;
  }
}
